import React, { Component } from 'react';
import { Col, Row, Form, Input, Radio } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import DatePickerString from 'components/common/input/DatePickerString';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormDocOut extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label filter-bar" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24} type="flex">
          <Col span={6}>
            <Form.Item label="Số công văn">{getFieldDecorator('id')(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngày đi ">
              {getFieldDecorator('docOutDate')(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đơn vị gởi">{getFieldDecorator('sentUnit')(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngày vào sổ">
              {getFieldDecorator('noteDate')(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Nội dung">{getFieldDecorator('docContent')(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Nơi nhận">{getFieldDecorator('recipientPlace')(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Nơi lưu">{getFieldDecorator('savedPlace')(<Input />)}</Form.Item>
          </Col>
          <Col span={6} className="is-complete-radio">
            <Form.Item style={{ marginBottom: 0 }}>
              {getFieldDecorator('isCompleted', {
                initialValue: null
              })(
                <Radio.Group>
                  <Radio value={null}>Tất cả</Radio>
                  <Radio value="true">Đã xử lý</Radio>
                  <Radio value="false">Chưa xử lý</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ display: 'flex', flexDirection: 'row-reverse' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormDocOut;



